<template>
  <div class="font-times">
    <!-- FORM -->
    <div class="container">
      <div class="row">
        <div class="col-12">
          <button
            type="button"
            @click="$store.dispatch('logout')"
            class="btn btn-sm btn-style9 float-right m-3"
            style="background: red"
          >
            <span class="typcn typcn-arrow-right"></span>
          </button>
          <img
            src="@/static/icon.png"
            class="kinoLightBox img-thumbnail float-left mt-3 mr-4"
            style="width: 50px"
          />
          <p class="ml-3 font-bold text-2xl pl-3 pt-3 font-italic font-times">
            ADMIN
          </p>
          <hr class="style13" />
          <div class="p-2 rounded-lg shadow-lg">
            <div class="row">
              <div class="col-4 mt-3 text-center">
                <button
                  @click="$router.push('/admin/kategori')"
                  type="button"
                  class="btn btn-sm btn-style9 p-3"
                >
                  <span class="typcn typcn-th-menu text-2xl"></span>
                </button>
                Kategori
              </div>
              <div class="col-4 mt-3 text-center">
                <button
                  @click="$router.push('/admin/food')"
                  type="button"
                  class="btn btn-sm btn-style9 p-3"
                >
                  <span class="typcn typcn-news text-2xl"></span>
                </button>
                <br />
                Item
              </div>
              <div class="col-4 mt-3 text-center">
                <button
                  @click="$router.push('/admin/notif')"
                  type="button"
                  class="btn btn-sm btn-style9 p-3"
                >
                  <span class="typcn typcn-mail text-2xl"></span>
                </button>
                Notifikasi
              </div>

              <div class="col-4 mt-3 text-center">
                <button
                  @click="$router.push('/admin/list')"
                  type="button"
                  class="btn btn-sm btn-style9 p-3"
                >
                  <span class="typcn typcn-dropbox text-2xl"></span>
                </button>
                Pesanan
              </div>
              <div class="col-4 mt-3 text-center">
                <button
                  @click="$router.push('/admin/app')"
                  type="button"
                  class="btn btn-sm btn-style9 p-3"
                >
                  <span class="typcn typcn-cog-outline text-2xl"></span>
                </button>
                Setting
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row justify-content-center">
      <div class="col-md-8 bg-white p-4 mt-5 shadow-lg">
        <p class="pt-3 font-weight-bold text-center">ITEM</p>

        <div class="row" v-if="show">
          <div class="offset-sm-2 col-sm-8">
            <form
              action
              @submit.prevent="
                aksi == 'insert'
                  ? insert()
                  : aksi == 'update'
                  ? update()
                  : remove()
              "
            >
              <div class="card">
                <div class="card-header">ITEM</div>
                <div class="card-body">
                  <!-- FORM -->
                  <tr>
                    <td class="text-uppercase text-sm">id_</td>
                    <td>&nbsp;:&nbsp;</td>
                    <td style="width: 90%; padding-bottom: 5px">
                      <div class="sm-form">
                        <input
                          type="text"
                          placeholder="id_"
                          id="id"
                          name="id"
                          class="form-control form-control-sm"
                          v-model="vdata.id"
                          disabled
                        />
                      </div>
                    </td>
                  </tr>

                  <tr>
                    <td class="text-uppercase text-sm">Nama_Item</td>
                    <td>&nbsp;:&nbsp;</td>
                    <td style="width: 90%; padding-bottom: 5px">
                      <div class="sm-form">
                        <input
                          type="text"
                          placeholder="nama_item"
                          id="nama_makanan"
                          name="nama_makanan"
                          class="form-control form-control-sm"
                          v-model="vdata.nama_makanan"
                          required
                        />
                      </div>
                    </td>
                  </tr>

                  <tr>
                    <td class="text-uppercase text-sm">jumlah_</td>
                    <td>&nbsp;:&nbsp;</td>
                    <td style="width: 90%; padding-bottom: 5px">
                      <div class="sm-form">
                        <input
                          type="text"
                          placeholder="jumlah_"
                          id="jumlah"
                          name="jumlah"
                          class="form-control form-control-sm"
                          v-model="vdata.jumlah"
                          required
                        />
                      </div>
                    </td>
                  </tr>

                  <tr>
                    <td class="text-uppercase text-sm">harga_</td>
                    <td>&nbsp;:&nbsp;</td>
                    <td style="width: 90%; padding-bottom: 5px">
                      <div class="sm-form">
                        <input
                          type="text"
                          placeholder="harga_"
                          id="harga"
                          name="harga"
                          class="form-control form-control-sm"
                          v-model="vdata.harga"
                          required
                        />
                      </div>
                    </td>
                  </tr>

                  <tr>
                    <td class="text-uppercase text-sm">Kategori</td>
                    <td>&nbsp;:&nbsp;</td>
                    <td style="width: 90%; padding-bottom: 5px">
                      <select class="form-control" v-model="vdata.kategori">
                        <option
                          v-for="(item, index) in kategori"
                          :key="index + 'kategori'"
                        >
                          {{ item.kategori }}
                        </option>
                      </select>
                    </td>
                  </tr>
                  <tr>
                    <td class="text-uppercase text-sm">gambar_</td>
                    <td>&nbsp;:&nbsp;</td>
                    <td style="width: 90%; padding-bottom: 5px">
                      <input type="file" id="filenya" @input="uploadphoto" />
                      <!-- <button type="button" @click="kirim" class="btn btn-sm btn-primary  "><span class="typcn typcn-upload"></span> Upload! </button> -->
                      <!-- <div class="sm-form" style="display:none;">
                  <input
                    type="text"
                    placeholder="gambar_"
                    id="gambar"
                    name="gambar"
                    class="form-control form-control-sm"
                    v-model="vdata.gambar"
                  required
              
                  >
                </div> -->
                    </td>
                  </tr>
                  <!-- FORM -->
                  <!-- SUBMIT -->
                  <br />
                  <div class="text-center">
                    <img
                      v-if="vdata.gambar"
                      :src="vdata.gambar"
                      alt=""
                      style="width: 100px; height: 100px"
                      v-viewer
                    />
                  </div>
                  <div class="text-center">
                    <button
                      type="submit"
                      v-show="aksi == 'insert'"
                      class="btn btn-sm btn-outline-primary ml-2 font-times text-uppercase"
                    >
                      <span class="typcn typcn-edit"></span>
                      {{
                        aksi == "insert"
                          ? "insert"
                          : aksi == "update"
                          ? "update"
                          : "remove"
                      }}
                    </button>
                    <button
                      type="submit"
                      v-show="aksi != 'insert'"
                      @click="aksi = 'update'"
                      class="btn btn-sm btn-outline-success ml-2 font-times text-uppercase"
                    >
                      <span class="typcn typcn-edit"></span> Update
                    </button>
                    <button
                      type="submit"
                      v-show="aksi != 'insert'"
                      @click="aksi = 'remove'"
                      class="btn btn-sm btn-outline-danger ml-2 font-times text-uppercase"
                    >
                      <span class="typcn typcn-edit"></span> Delete
                    </button>
                  </div>
                </div>
              </div>
              <!-- SUBMIT -->
            </form>
            <hr class="style13" />
          </div>
        </div>
        <hr class="style16" />

        <!-- <tables :datatable="datanya" :unless="['fname','lname']" /> -->
        <button
          type="button"
          class="btn btn-sm text-white bg-dark mr-2"
          v-if="!ready"
          @click="show = !show"
          style="width: 40px; height: 30px"
        >
          <span class="typcn typcn-arrow-maximise"></span>
        </button>
        <div style="overflow-x: auto; width: 100%" v-if="ready">
          <!-- MODAL -->
          <!-- ======================================================= -->
          <!-- DATATABLE MULAI DISINI -->
          <!-- SEARCH INPUT -->
          <div class="container-fluid">
            <div class="row p-2">
              <div class="col-sm-4 text-center">
                <button
                  type="button"
                  class="btn btn-sm text-white bg-dark mr-2"
                  @click="show = !show"
                  style="width: 40px; height: 30px"
                >
                  <span class="typcn typcn-arrow-maximise"></span>
                </button>
                <button
                  type="button"
                  @click="
                    aksi = 'insert';
                    clearInput();
                    show = true;
                  "
                  class="text-uppercase font-times btn btn-sm btn-success"
                >
                  <span class="typcn typcn-plus"></span>Tambah
                </button>
                &nbsp;
              </div>
              <div class="col-sm-4 row">
                <!-- SELECTION  -->
                <div class="col-6">
                  <select
                    class="form-control"
                    style="width: 80px; height: 30px; font-size: 12px"
                    v-model="selected"
                  >
                    <option>10</option>
                    <option>20</option>
                    <option>50</option>
                    <option>100</option>
                    <option :value="1000000">Semua</option>
                  </select>
                </div>
                <!-- SELECTION -->
                <div class="col-3"></div>
                <div class="col-3">
                  <div class="row">
                    <div class="col-6">
                      <div class="tips">
                        <button
                          type="button"
                          @click="print2"
                          class="btn btn-sm btn-outline-danger"
                        >
                          <span class="typcn typcn-document-text"></span>
                        </button>
                        <span class="tipstextB">Print Laporan</span>
                      </div>
                    </div>
                    <div class="col-6">
                      <a
                        :href="`https://akbarmaliki.github.io/infolayanansjs/#/excel?data=${JSON.stringify(
                          td
                        )
                          .split('&')
                          .join('%26')}`"
                        class="btn-sm btn-success"
                        ><span class="typcn typcn-chart-bar"></span
                      ></a>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-sm-4">
                <!-- SEARCH -->
                <div class="sm-form">
                  <input
                    type="text"
                    id="search"
                    name="search"
                    class="form-control form-control-sm"
                    placeholder="Cari data ..."
                    v-model="search"
                  />
                </div>
                <!-- SEARCH -->
              </div>
            </div>
          </div>
          <!-- TABLE -->
          <div
            class="table-responsive-sm font-times p-3"
            style="box-shadow: 1px 4px 6px black; overflow: scroll"
            v-dragscroll
          >
            <template>
              <!-- DEKSTOP -->
              <table
                class="table-print table-sm table-bordered table-striped table-hover table-print border-black"
                style="
                  overflow-x: scroll;
                  overflow-y: hidden;
                  height: 80px;
                  white-space: nowrap;
                "
              >
                <!-- TABLE STYLE -->
                <thead class="mdb-color" :style="{ background: thbackground }">
                  <tr class="text-white" style="overflow: scroll; color: white">
                    <th class :style="{ color: thcolor }">#</th>

                    <th
                      scope="col"
                      class="th-print cursor-pointer uppercase"
                      style="font-size: 21px"
                      @click="sort(item)"
                      v-for="(item, index) in thnya"
                      :key="index + 'th'"
                      v-show="!less.includes(item)"
                    >
                      <span :style="{ color: thcolor }" style="font-size: 13px">
                        {{ item }}
                        <template v-if="orderWith">
                          <span v-show="orderBy == item">
                            <span class="typcn typcn-arrow-down"></span>
                          </span>
                        </template>
                        <template v-else>
                          <span v-show="orderBy == item">
                            <span class="typcn typcn-arrow-up"></span>
                          </span>
                        </template>
                      </span>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index3) in td" :key="index3 + 'td'">
                    <td>{{ index3 + temp1 + 1 }}</td>
                    <no-ssr>
                      <v-touch
                        tag="td"
                        v-on:tap="
                          ambil(item, index3);
                          modal = true;
                          aksi = 'update';
                          show = true;
                        "
                        class="cursor-pointer"
                        v-for="(item2, indexs) in thnya"
                        :key="indexs + 'th2'"
                        v-html="item[item2]"
                        v-show="!less.includes(item2)"
                      ></v-touch>
                      <!-- item[item2].split('-').length>2 && item[item2].indexOf('20')!=-1 ? format(item[item2]):item[item2]  -->
                    </no-ssr>
                  </tr>
                </tbody>
              </table>
            </template>
            <!-- FUNGSI DIBAWAH TABEL SEPERTI PAGINATION -->
            <div class="float-right">
              <template v-if="datanya.length >= selected">
                <button
                  type="button"
                  @click="pagPrev"
                  class="btn btn-sm btn-outline-info roundec-circle"
                >
                  Prev
                </button>
                &nbsp;
                <button
                  type="button"
                  @click="pagNext"
                  class="btn btn-sm btn-info roundec-circle"
                >
                  Next
                </button>
              </template>
              <template v-else>
                <button
                  type="button"
                  @click="pagPrev"
                  class="btn btn-sm btn-black roundec-circle"
                  disabled
                >
                  Prev
                </button>
                &nbsp;
                <button
                  type="button"
                  @click="pagNext"
                  class="btn btn-sm btn-black roundec-circle"
                  disabled
                >
                  Next
                </button>
              </template>
            </div>
          </div>
        </div>
        <div v-else>
          <p class="text-center">Loading Table ...</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var id = require("date-fns/locale/id");
import downloadExcel from "vue-json-excel";
import upload from "@/components/Single/UploadsNo.vue";
      import imageCompression from "browser-image-compression";
import axios from "axios";
import daycaca from "daycaca";
import firebase from "firebase";
let db = firebase.firestore();
export default {
  components: {
    upload,
    downloadExcel,
  },
  data() {
    return {
      // style
      thbackground: "initial",
      thcolor: "asd",
      // valdatable
      datanya: [],
      order: ["id", "nama_makanan", "jumlah", "harga", "kategori", "gambar"],
      vdata: {},
      ready: false,
      show: false,
      search: "",
      pilih: { name: "taufik", hobby: "coding" },
      thnya: {},
      err: false,
      file: '',
      checkedItem: [],
      less: [, "_id", "id"],
      // modal
      modal: false,
      modalAction: "preview",
      // pagination
      selected: 10,
      pagination: 1,
      temp1: 0,
      temp2: 10,
      orderBy: "",
      orderWith: true,
      //form
      aksi: "insert",
      formOn: false,
      updateOn: false,
      kategori: [],
    };
  },
  methods: {
    getKategori() {
      db.collection("kategori").onSnapshot((res) => {
        let data = res.docs.map((e) => e.data());
        this.kategori = data;
      });
    },
    kirim() {
      let formData = new FormData();
      const timestamp = Date.now() / 1000;
      let file = document.querySelector("#datafile");
      formData.append("file", file.files[0]);
      formData.append("api_key", "395872872147188");
      //  formData.append("file", this.file);
      // formData.append("public_id", "test");
      formData.append("timestamp", timestamp);
      formData.append("upload_preset", "nfh8kzxy");
      axios
        .post(`https://api.cloudinary.com/v1_1/borneojs/image/upload`, formData)
        .then((result) => {
          console.log(result);
          // result.data.secure_url
          this.vdata.gambar = result.data.secure_url;
          alert("Gambar berhasil di upload !");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async uploadphoto() {
      let that = this;
      let el = document.querySelector("#filenya"); // berikan id pada input file
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        let file = el.files[0];
        let fd = new FormData();
        const compressedFile = await imageCompression(file, options);
        fd.append("file", compressedFile);
        fd.append("data", `select * from tbuser`); //database setting di server
        fd.append("secret", `dsdxxoi4#$(*#sdsaaada@#`); //database setting di server
        axios
          .post("https://upload.egov.balangankab.go.id/upload1.php", fd)
          .then((res) => {
            that.$forceUpdate();
            console.log("https://upload.egov.balangankab.go.id" + res.data);
            that.vdata.gambar =
              "https://upload.egov.balangankab.go.id" + res.data;
            alert("Gambar berhasil di upload !");
          });
      } catch (error) {
        console.log(error);
      }
    },
    async uploadphoto2(e) {
      let that = this;
      console.log(e.target.files[0].name);
      await daycaca.compress(e.target.files[0], 50, (data) => {
        return that
          .$urltofile(data, e.target.files[0].name, e.target.files[0].type)
          .then((res) => {
            let file = res; // tambahkan data set kosong file:""
            that.$nuxt.$emit("busy", true);
            setTimeout(() => {
              that.$nuxt.$emit("busy", false);
              let fd = new FormData();
              fd.append("file", file);
              fd.append("secret", "dsdxxoi4#$(*#sdsaaada@#");
              if (confirm("Apakah yakin proses upload photo ?")) {
                axios
                  .post(
                    "http://upload.egovteam.balangankab.go.id/upload1.php",
                    fd
                  )
                  .then((res) => {
                    that.$forceUpdate();
                    console.log(
                      "http://upload.egovteam.balangankab.go.id" + res.data
                    );
                    that.vdata.gambar =
                      "http://upload.egovteam.balangankab.go.id" + res.data;
                    alert("Gambar berhasil di upload !");
                  });
              }
            }, 2000);
            return res;
          });
      });
    },
    insert() {
      delete this.vdata.id;
      db.collection(`food/`)
        .doc()
        .set(this.vdata)
        .then((res) => {
          console.log("berhasil");
          alert("simpan data berhasil!");
        });
    },
    update() {
      db.collection(`food/`)
        .doc(this.pilih.id)
        .set(this.vdata, { merge: true })
        .then((res) => {
          alert("update data berhasil!");
        });
    },
    remove() {
      if (confirm("Apakah yakin menghapus data?")) {
        db.collection(`food/`)
          .doc(this.pilih.id)
          .delete()
          .then((res) => {
            alert("delete data berhasil!");
          });
      }
    },
    refreshData() {
      let that = this;
      db.collection(`food/`).onSnapshot((snap) => {
        let arr = [];
        snap.forEach((e) => {
          arr.push({ id: e.id, ...e.data() });
        });
        setTimeout(() => {
          (function () {
            var thElm;
            var startOffset;
            Array.prototype.forEach.call(
              document.querySelectorAll("table th"),
              function (th) {
                th.style.position = "relative";
                var grip = document.createElement("div");
                grip.innerHTML = "&nbsp;";
                grip.style.top = 0;
                grip.style.right = 0;
                grip.style.bottom = 0;
                grip.style.width = "5px";
                grip.style.position = "absolute";
                grip.style.cursor = "col-resize";
                grip.addEventListener("mousedown", function (e) {
                  thElm = th;
                  startOffset = th.offsetWidth - e.pageX;
                });
                th.appendChild(grip);
              }
            );
            document.addEventListener("mousemove", function (e) {
              if (thElm) {
                thElm.style.width = startOffset + e.pageX + "px";
              }
            });
            document.addEventListener("mouseup", function () {
              thElm = undefined;
            });
          })();
          let hasil = [];
          let obj = {};
          arr.forEach((e, index) => {
            that.order.forEach((e) => {
              obj[e] = arr[index][e];
            });
            hasil.push(obj);
            obj = {};
          });
          this.datanya = hasil;
          let keys = this.order;
          keys = this.$_.difference(keys, this.unless);
          this.thnya = keys;
        }, 1000);
      });
    },
    ambil(item, index) {
      window.scrollTo(0, 0);
      let that = this;
      this.updateOn = true;
      this.formOn = true;
      let clone = this.$_.clone(item);
      // clone["TGL_LHR"] = this.$datefns.format(
      //   new Date(clone["TGL_LHR"]),
      //   "YYYY-MM-dd"
      // );
      console.log(clone);
      this.vdata = clone;
      this.pilih = item;
    },
    clearInput() {
      this.formOn = true;
      let keys = Object.keys(this.vdata);
      keys.forEach((key) => {
        this.vdata[key] = "";
      });
    },

    //!==================
    //! hapus dari sini
    //! =================
    checkedAll() {
      console.log(this.checkedItem);
    },
    susun(datas) {
      let that = this;
      let data = datas;
      let order = ["id", "nama_makanan", "jumlah", "harga", "gambar"];
      let hasil = [];
      let obj = {};
      data.forEach((e, index) => {
        order.forEach((e) => {
          obj[e] = data[index][e];
        });
        hasil.push(obj);
        obj = {};
      });
      return hasil;
    },
    print1() {
      this.$router.push(`/laporan/perdata?less=${this.less.toString()}`);
      this.$store.state.print2 = [];
      this.$store.state.print2.push(this.pilih);
      this.$store.state.print2 = this.susun(this.$store.state.print2);
      alert("print satu");
    },
    print2() {
      this.$router.push(`/laporan/print?less=${this.less.toString()}`);
      let that = this;
      this.$store.state.print2 = this.susun(this.td);
      alert("print banyak");
    },
    linknya(hasil) {
      console.log("link", hasil);
      this.vdata.Gambars = hasil;
      this.$forceUpdate();
    },
    isDate(value) {
      var dateFormat;
      if (toString.call(value) === "[object Date]") {
        return true;
      }
      if (typeof value.replace === "function") {
        value.replace(/^s+|s+$/gm, "");
      }
      dateFormat = /(^d{1,4}[.|\/|-]d{1,2}[.|\/|-]d{1,4})(s*(?:0?[1-9]:[0-5]|1(?=[012])d:[0-5])ds*[ap]m)?$/;
      return dateFormat.test(value);
    },
    // MODAL
    format(date) {
      return this.$datefns.format(date, "DD MMMM YYYY", {
        locale: id,
      });
    },
    distanceToNow() {
      return this.$datefns.distanceInWordsToNow(
        new Date(2015, 0, 1, 0, 0, 15),
        {
          locale: id,
          includeSeconds: true,
        }
      );
      //=> "3 days ago"
    },
    // DATATABLE
    pagPrev() {
      this.pagination = this.pagination - 1;
      if (this.pagination == 0) {
        this.pagination = 1;
      }
      this.temp2 = this.pagination * this.selected;
      this.temp1 = Math.ceil(this.temp2) - this.selected;
    },
    pagNext() {
      this.pagination = this.pagination + 1;
      if (this.pagination > this.bagi + 1) {
        this.pagination = Math.round(this.bagi);
      }
      this.temp2 = this.pagination * this.selected;
      this.temp1 = Math.ceil(this.temp2) - this.selected;
    },
    sort(item) {
      this.orderBy = item;
      this.orderWith = !this.orderWith;
    },
    //!==================
    //! hapus sampai sini
    //! =================
  },
  props: {
    unless: {
      type: Array,
      required: false,
      default: () => ["gone"],
    },
    date: {
      type: Array,
      required: false,
      default: () => ["ditanyakan"],
    },
  },
  watch: {
    datanya() {
      if (this.datanya.length > 0) {
        this.ready = true;
      }
    },
    selected() {
      this.temp2 = this.pagination * this.selected;
      this.temp1 = this.temp2 - this.selected;
    },
    search() {
      this.pagination = 1;
      this.temp2 = this.pagination * this.selected;
      this.temp1 = this.temp2 - this.selected;
    },
  },
  computed: {
    // DATATABLE
    bagi() {
      let data = this.datanya;
      data = data.length / 10;
      console.log(data);
      return data;
    },
    th() {
      let data = this.datanya;
      let keys = Object.keys(data[0]);
      keys = this.$_.difference(keys, this.unless);
      return keys;
    },
    td() {
      let that = this;
      let data = this.datanya;
      //! order fungsi
      if (this.orderBy.length > 0) {
        let ordernya = this.orderWith ? "asc" : "desc";
        data = this.$_.orderBy(data, [this.orderBy], [ordernya]);
      }
      //! search fungsi
      let keys = this.thnya;
      data = data.filter((v, i, a) => {
        let hasil = false;
        keys.filter((key) => {
          if (typeof v[key] == "string") {
            if (v[key].toLowerCase().indexOf(this.search.toLowerCase()) != -1) {
              hasil = true;
            }
          } else {
            if (v[key] != null) {
              if (v[key].toString().indexOf(this.search) != -1) {
                hasil = true;
              }
            } else {
            }
          }
        });
        if (hasil) return v;
      });
      //! cari shortcut contoh
      // data = data.filter((v, i, a) => {
      //   let hasil = false;
      //   console.log(v.id_kelas==that.carikelas)
      //     if(v['id_kelas']==this.carikelas){
      //       return v
      //     }else if(this.carikelas==''){
      //       return v
      //     }
      // });
      data = data.map((e) => {
        let obj = {};
        let kunci = Object.keys(e);
        kunci = that.$_.difference(kunci, that.unless);
        kunci.forEach((a, index) => {
          if (kunci.find((e) => e == that.date[index])) {
          } else {
            // obj[a] = 'tanggal'
          }

          obj[a] = e[a];
          // typeof e[a] != "string"
          //   ? e[a]
          //   : new Date(e[a]) !== "Invalid Date" && !isNaN(new Date(e[a]))
          //   ? that.isDate(e[a])
          //     ? that.format(e[a])
          //     : e[a]
          //   : e[a];
        });
        return obj;
      });
      data = data.slice(this.temp1, this.temp2);
      return data;
    },
  },
  mounted() {
    let that = this;
    this.getKategori();
    this.refreshData();
  },
};
</script>